import React, {FC} from 'react';
import styles from './RangeInput.module.scss';

export interface Props {
    id?: string,
    min?: number,
    max?: number,
    value?: number,
    step?: number,
    name?: string,
    title?: string,
    disabled?: boolean
    onChange?: React.ChangeEventHandler<HTMLInputElement>
}

const RangeInput: FC<Props> = ({ ...props }) => {
    return (
        <input
            className={styles['range-input']}
            type="range"
            id={props.id}
            name={props.name}
            min={props.min}
            max={props.max}
            step={props.step}
            value={props.value}
            disabled={props.disabled}
            title={props.title}
            onChange={props.onChange}
        />
    );
};

export default RangeInput;
